.swiper-page {
  display: flex;
  flex-direction: column;
  // gap: 2rem;
}

.hero-swiper {
  height: 100vh;
  position: relative;
  &__title {
    font-size: 4rem;
    font-weight: 700;
    text-align: center;
    position: absolute;
    color: $tertiary-color;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 3;
  }

  &__swiper.swiper {
    z-index: 2;
    height: 100vh;
    .swiper-wrapper {
      min-height: 100%;
    }
    .swiper-slide {
      width: 100%;
      max-width: 100%;
      height: 100%;
      position: relative;
      &::before {
        content: "";
        display: block;
        position: absolute;
        inset: 0;
        width: 100%;
        height: 100%;
        z-index: 2;
        background-color: rgba($color: #000000, $alpha: 0.7);
        // -webkit-backdrop-filter: blur(5px);
      }
    }
  }

  &__swiper-slide-video {
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 1;
  }
}

.clients-swiper {
  padding: 2rem 1rem 2rem;
  &__swiper.swiper {
    overflow: hidden;
    // width: 80vw;
    // border-radius: 9999px;
    // box-shadow: 0 1px 10px 1px rgba(145, 144, 144, 0.25);

    .swiper-wrapper {
      transition-timing-function: linear !important;
      width: 100%;
      height: 100%;
      // border-radius: 9999px;
    }
    .swiper-slide {
      display: flex;
      align-items: center;
      width: auto !important;
      height: auto;
      padding: 0 2rem;
    }
  }
  &__swiper-slide-img {
    width: auto;
    max-width: unset;
    max-height: 66px;
    object-fit: cover;
    filter: grayscale(100%);
    transition: 300ms filter ease-in-out;
    &:hover {
      filter: grayscale(0);
    }
  }
}

.tabs-swiper {
  margin-block: 4rem;
  // min-height: 200px;
  padding: 0.5rem;
  &__swiper {
    height: fit-content;
  }
  &__swiper-wrapper {
    padding: 0.5rem;
  }
  &__swiper-slide {
    transition-property: transform, height;
    transition: height 0.5s ease-in-out;
    border: 2px solid blue;
    // height: fit-content;
    // min-height: auto !important;
  }
  &__swiper-slide-img {
    &.low {
      height: 100px;
    }
    &.mid {
      height: 200px;
    }
    &.high {
      height: 400px;
    }
  }
}
